import React, { useState, useRef, useEffect } from "react";
import { connect } from "react-redux";
import { setSearchText } from "../../store/actions/actions";
import "./Search.css";
import searchIcon from "./Search.png";
import clearIcon from "./clear.PNG";

const Search = (props) => {
  const { dispatchSearchText, searchText: intialSearchValue } = props;
  const [searchText, setSearchText] = useState(intialSearchValue);
  const inputEl = useRef(null);

  const handleSearchTextChange = (e) => {
    setSearchText(e.target.value);
  };

  const handleSearchClick = () => {
    dispatchSearchText(searchText);
  };

  const handleClearSearch = () => {
    setSearchText("");
    inputEl.current.focus();
  };

  useEffect(() => {
    dispatchSearchText(searchText);
  }, [searchText, dispatchSearchText]);

  return (
    <div className="search__container">
      <div className="search__input">
        <img src={searchIcon} alt="search" />
        <input
          ref={inputEl}
          type="text"
          value={searchText}
          onChange={handleSearchTextChange}
          placeholder="Search by Item Name, SKU id…"
        />
        <img
          className="search__clear"
          src={clearIcon}
          onClick={handleClearSearch}
          alt="clear search"
        />
      </div>
      <div className="search__button">
        <button onClick={handleSearchClick}>Search</button>
      </div>
    </div>
  );
};

const mapStateToProps = ({ searchText }) => ({
  searchText,
});

const mapDispatchToProps = (dispatch) => ({
  dispatchSearchText: (searchText) => dispatch(setSearchText({ searchText })),
});

export default connect(mapStateToProps, mapDispatchToProps)(Search);
